import Layout from "./components/layout/Layout";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import NewReport from "./pages/NewReport";
import FhirAuthoriser from "./components/fhir/FhirAuthoriser";

const GH_REPOSITORY = process.env.REACT_APP_GH_REPOSITORY;

function App() {
  return (
    <BrowserRouter basename={GH_REPOSITORY}>
      <Layout>
        <Routes>
          <Route path="/" element={<FhirAuthoriser />} />
          <Route path="/add_report" element={<NewReport />} />
        </Routes>
      </Layout>
    </BrowserRouter>
  );
}

export default App;
